<template>
  <el-tooltip
    :content="text"
    :placement="placement"
    :show-after="showAfter"
    :hide-after="hideAfter"
    :enterable="enterable"
    :disabled="!isEllipsis"
  >
    <span ref="textRef" :class="['ellipsis-text', className]" @mouseenter="checkEllipsis">
      <slot>{{ text }}</slot>
    </span>
  </el-tooltip>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'

interface Props {
  text: string
  placement?:
    | 'top'
    | 'top-start'
    | 'top-end'
    | 'bottom'
    | 'bottom-start'
    | 'bottom-end'
    | 'left'
    | 'right'
  showAfter?: number
  hideAfter?: number
  enterable?: boolean
  className?: string
}

withDefaults(defineProps<Props>(), {
  placement: 'top',
  showAfter: 0,
  hideAfter: 0,
  enterable: false,
  className: '',
})

const textRef = ref<HTMLElement | null>(null)
const isEllipsis = ref(false)

// 检查文本是否溢出
const checkEllipsis = () => {
  if (!textRef.value) return
  const element = textRef.value
  isEllipsis.value = element.scrollWidth > element.offsetWidth
}

// 组件挂载后检查一次
onMounted(() => {
  checkEllipsis()
})
</script>

<style scoped lang="less">
.ellipsis-text {
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  vertical-align: bottom;
}
</style>
