<script lang="ts">
export default {
  name: 'PatientInfo',
}
</script>

<script setup lang="ts">
import PageContainer from '@/components/pageContainer/PageContainer.vue'
import EllipsisText from '@/components/EllipsisText.vue'
import { onMounted, computed } from 'vue'
import { useRouter } from 'vue-router'
import { Search, WarningFilled, CircleCloseFilled, Money } from '@element-plus/icons-vue'
import { usePatientInfoStore } from '@/stores/patientInfoStore'
import { storeToRefs } from 'pinia'
import dayjs from 'dayjs'

const router = useRouter()
const patientStore = usePatientInfoStore()

// 从 store 中解构需要的状态和方法
const { searchForm, loading, patientList } = storeToRefs(patientStore)
const { fetchPatientList, resetSearchForm } = patientStore

// 搜索类型选项
const searchTypeOptions = [
  { label: '病患姓名', value: 'name' },
  { label: '医生姓名', value: 'doctorName' },
  { label: '血型', value: 'bloodType' },
  { label: '住院号', value: 'id' },
  { label: '申请单号', value: 'applicationNumber' },
]

// 手术状态选项
const surgeryStatusOptions = [
  { label: '全部', value: '' },
  { label: '手术中', value: 'in_progress' },
  { label: '待手术', value: 'pending' },
  { label: '已完成', value: 'completed' },
]

// 交接状态选项
const handOverStatusOptions = [
  { label: '全部', value: '' },
  { label: '已交接', value: 'completed' },
  { label: '未交接', value: 'pending' },
]

// 手术间选项
const operatingRoomOptions = [
  { label: '全部', value: '' },
  { label: '04手术间', value: '04' },
  { label: '13手术间', value: '13' },
  { label: '14手术间', value: '14' },
  { label: '15手术间', value: '15' },
]

// 获取手术状态对应的颜色
const getStatusColor = (status: string) => {
  const colorMap: Record<string, string> = {
    pending: '#ff9900',
    in_progress: '#409eff',
    completed: '#67c23a',
  }
  return colorMap[status] || '#909399'
}

// 计算搜索框的提示文本
const searchPlaceholder = computed(() => {
  const selectedType = searchTypeOptions.find((item) => item.value === searchForm.value?.searchType)
  return `请输入${selectedType?.label || ''}`
})

// 处理搜索
const handleSearch = () => {
  fetchPatientList()
}

// 处理重置
const handleReset = () => {
  resetSearchForm()
  fetchPatientList()
}

// 处理卡片点击
const handleCardClick = async (patient: Patient_API.PatientInfo) => {
  // 使用 replace 而不是 push，避免历史记录堆积
  await router.replace({
    name: 'PatientDetail',
    params: {
      id: patient.id,
      name: patient.name,
    },
  })
}

// 组件挂载时获取数据
onMounted(() => {
  fetchPatientList()
})

// 格式化日期
const formatDate = (date: string) => {
  return dayjs(date).format('YYYY-MM-DD HH:mm')
}

// 获取手术类型标签
const getSurgeryTypeLabel = (type: string) => {
  const typeMap: Record<string, string> = {
    elective: '择期',
    emergency: '急诊',
    day: '日间',
  }
  return typeMap[type] || type
}
</script>

<template>
  <PageContainer>
    <!-- 搜索表单 -->
    <el-form :model="searchForm" class="search-form" inline>
      <!-- 手术时间区间 -->
      <el-form-item label="手术时间">
        <el-date-picker
          v-model="searchForm.surgeryTimeStart"
          type="datetime"
          placeholder="开始时间"
          format="YYYY-MM-DD HH:mm"
          value-format="YYYY-MM-DD HH:mm"
          :default-time="new Date(2000, 1, 1, 8, 0, 0)"
          clearable
          class="time-picker"
        />
        <span class="time-separator">至</span>
        <el-date-picker
          v-model="searchForm.surgeryTimeEnd"
          type="datetime"
          placeholder="结束时间"
          format="YYYY-MM-DD HH:mm"
          value-format="YYYY-MM-DD HH:mm"
          :default-time="new Date(2000, 1, 1, 23, 59, 59)"
          clearable
          class="time-picker"
        />
      </el-form-item>

      <!-- 手术间 -->
      <el-form-item label="手术间" :span="24" class="width300">
        <el-select v-model="searchForm.operatingRoom" placeholder="请选择手术间" clearable>
          <el-option
            v-for="item in operatingRoomOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>

      <!-- 手术状态 -->
      <el-form-item label="手术状态" class="width300">
        <el-select v-model="searchForm.surgeryStatus" placeholder="请选择手术状态" clearable>
          <el-option
            v-for="item in surgeryStatusOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>

      <!-- 交接状态 -->
      <el-form-item label="交接状态" class="width300">
        <el-select v-model="searchForm.handOverStatus" placeholder="请选择交接状态" clearable>
          <el-option
            v-for="item in handOverStatusOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>

      <!-- 搜索类型和值 -->
      <el-form-item>
        <div class="search-input-group">
          <el-select
            v-model="searchForm.searchType"
            placeholder="搜索类型"
            class="search-type-select"
          >
            <el-option
              v-for="item in searchTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
          <el-input
            v-model="searchForm.searchValue"
            :placeholder="searchPlaceholder"
            clearable
            class="search-value-input"
          />
        </div>
      </el-form-item>

      <!-- 操作按钮 -->
      <el-form-item>
        <el-button type="primary" :icon="Search" @click="handleSearch">搜索</el-button>
        <el-button @click="handleReset">重置</el-button>
      </el-form-item>
    </el-form>

    <!-- 患者卡片列表 -->
    <div class="patient-cards" v-loading="loading">
      <el-card
        v-for="patient in patientList"
        :key="patient.id"
        class="patient-card"
        @click="handleCardClick(patient)"
      >
        <!-- 卡片标题栏 -->
        <div
          class="card-header"
          :style="{ backgroundColor: getStatusColor(patient.surgeryStatus) }"
        >
          <div class="header-info">
            <span>{{ patient.ward }}</span>
            <el-divider direction="vertical" />
            <span>{{ patient.bedNumber }}</span>
            <el-divider direction="vertical" />
            <span>{{ patient.operatingRoom }}</span>
            <el-divider direction="vertical" />
            <span>第{{ patient.sequence }}台</span>
          </div>
          <div class="status-tag">
            {{
              surgeryStatusOptions.find((item) => item.value === patient.surgeryStatus)?.label ||
              '未知'
            }}
          </div>
        </div>

        <!-- 卡片内容 -->
        <div class="card-content">
          <!-- 患者标识图标 -->
          <div class="patient-flags">
            <el-tooltip v-if="patient.flags.isAllergic" content="过敏" placement="top">
              <el-icon class="warning-icon"><WarningFilled /></el-icon>
            </el-tooltip>
            <el-tooltip v-if="patient.flags.hasVTE" content="VTE风险" placement="top">
              <el-icon class="warning-icon"><CircleCloseFilled /></el-icon>
            </el-tooltip>
            <el-tooltip
              v-if="patient.flags.hivStatus === 'positive'"
              content="HIV阳性"
              placement="top"
            >
              <el-icon class="danger-icon"><CircleCloseFilled /></el-icon>
            </el-tooltip>
            <el-tooltip v-if="patient.flags.hasArrearage" content="欠费" placement="top">
              <el-icon class="warning-icon"><Money /></el-icon>
            </el-tooltip>
            <el-tooltip
              v-if="patient.flags.syphilisStatus === 'positive'"
              content="梅毒阳性"
              placement="top"
            >
              <el-icon class="danger-icon"><CircleCloseFilled /></el-icon>
            </el-tooltip>
          </div>

          <div class="patient-info">
            <!-- 基本信息行 -->
            <el-row class="main-info" :gutter="8">
              <el-col>
                <span class="patient-name">{{ patient.name }}</span>
                <span class="patient-gender-age">{{ patient.gender }} {{ patient.age }}岁</span>
                <span class="blood-type">{{ patient.bloodType }}</span>
              </el-col>
            </el-row>

            <!-- 其他信息网格 -->
            <el-row :gutter="16" class="info-grid">
              <el-col :span="12">
                <div class="info-item">
                  <span class="label">住院号：</span>
                  <EllipsisText :text="patient.id" class="value" />
                </div>
              </el-col>
              <el-col :span="12">
                <div class="info-item">
                  <span class="label">病案号：</span>
                  <EllipsisText :text="patient.medicalRecordNumber" class="value" />
                </div>
              </el-col>
              <el-col :span="12">
                <div class="info-item">
                  <span class="label">手术时间：</span>
                  <EllipsisText :text="formatDate(patient.surgeryTime)" class="value" />
                </div>
              </el-col>
              <el-col :span="12">
                <div class="info-item">
                  <span class="label">手术名称：</span>
                  <EllipsisText :text="patient.surgeryName" class="value" />
                </div>
              </el-col>
              <el-col :span="12">
                <div class="info-item">
                  <span class="label">手术类型：</span>
                  <EllipsisText :text="getSurgeryTypeLabel(patient.surgeryType)" class="value" />
                </div>
              </el-col>
              <el-col :span="12">
                <div class="info-item">
                  <span class="label">麻醉方式：</span>
                  <EllipsisText :text="patient.anesthesiaMethod" class="value" />
                </div>
              </el-col>
              <el-col :span="12">
                <div class="info-item">
                  <span class="label">手麻编号：</span>
                  <EllipsisText :text="patient.anesthesiaNumber" class="value" />
                </div>
              </el-col>
              <el-col :span="12">
                <div class="info-item">
                  <span class="label">护理等级：</span>
                  <EllipsisText :text="patient.nursingLevel" class="value" />
                </div>
              </el-col>
              <el-col :span="12">
                <div class="info-item">
                  <span class="label">医保类型：</span>
                  <EllipsisText :text="patient.insuranceType" class="value" />
                </div>
              </el-col>
              <el-col :span="12" v-if="patient.allergies">
                <div class="info-item">
                  <span class="label">过敏史：</span>
                  <EllipsisText :text="patient.allergies" class="value warning-text" />
                </div>
              </el-col>
            </el-row>

            <!-- 医护人员信息 -->
            <el-row :gutter="16" class="staff-info">
              <el-col :span="8">
                <div class="info-item">
                  <span class="label">手术医生：</span>
                  <EllipsisText :text="patient.surgeonName" class="value" />
                </div>
              </el-col>
              <el-col :span="8">
                <div class="info-item">
                  <span class="label">麻醉医生：</span>
                  <EllipsisText :text="patient.anesthesiologistName" class="value" />
                </div>
              </el-col>
              <el-col :span="8">
                <div class="info-item">
                  <span class="label">巡回护士：</span>
                  <EllipsisText :text="patient.circuitNurse" class="value" />
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
      </el-card>
    </div>
  </PageContainer>
</template>

